.container-app {
    position: relative;

    .container-main {
        background-color: #eeeeee;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        padding: 0.5rem 10rem 2rem;

        .layout-menu {
            width: 100%;
            background-color: #f8f9fa;
            padding: 2rem;
            box-shadow: 1px 1px 5px silver;
            display: flex;
            justify-content: space-between;

            .article-layout {
                box-shadow: 1px 1px 5px silver;
                width: calc(100% - 30% - 2rem);

                .list-title {
                    display: flex;
                    padding: 1rem 1.5rem;
                }
                
                .list-title,
                .menu-itens {
                    background-color: #ffffff;
                }
            }

            .menu-itens {
                padding: 0.5rem;

                div {
                    span {
                        padding: 0 1.5rem 0 0;
                        display: flex;
                        justify-content: space-between;

                        h2 {
                            margin: 0 0 1rem 0;
                        }
                    }
                }
            }
        }
    }

    .input-search {
        width: 100%;
        padding: 0.8rem;
        border: 1px solid silver;
        border-radius: 0.5rem;
        outline: 0;
        margin-bottom: 0.5rem;
        box-shadow: 1px 1px 5px silver;
        font-size: 14px;
    }
}

.message-allergy {
    p {
        padding: 1.5rem 0;
        border-top: 1px solid silver;
        border-bottom: 1px solid silver;
        display: block;
        text-align: center;
        color: var(--primary-color);
    }
}

/*botao de ver o carrinho no mobile*/
.container-footer {
    display: none;

    a {
        padding: 1.5rem 0;
        border-top: 1px solid silver;
        border-bottom: 1px solid silver;
        display: block;
        text-align: center;
        color: var(--primary-color);
    }

    button {
        background-color: var(--primary-color);    
        color: white;
        padding: 1rem 3rem;
        border-radius: 2rem;
        border: 1px solid transparent;
        margin: 1rem;
    }
}


@media (max-width:1280px) {
    .container-app {
        .container-main {
            padding: 0.5rem 8rem 2rem;
        }
    }
}


@media (max-width:1140px) {
    .container-app {
        .container-main {
            padding: 0.5rem 5rem 2rem;
        }
    }
}

@media (max-width:1045px) {
    .container-app {
        .container-main {
            .layout-menu {
                padding: 1rem;

                .article-layout {
                    width: calc(100% - 30% - 1rem);
                }
            }
        }
    }
}


@media (max-width:990px) {
    .container-app {
        .container-main {
            padding: 0.5rem 2rem 2rem;
        }
    }
}

@media (max-width:830px) {
    .container-app {
        .container-main {
            padding: 0.5rem 0;
            background-color: #ffffff;

            .input-search {
                margin: 0 1rem;
            }

            .layout-menu {
                padding: 0.5rem 0;
                box-shadow: none;
                background-color: #ffffff;

                .article-layout {
                    width: calc(100%);
                    box-shadow: none;
                }
            }
        }

        .container-footer {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }

        .container-cart {
            display: none;
        }
        
        .cart-mobile-open {
            overflow: hidden;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            background-color: #ffffff;

            .layout-mobile {
                text-align: center;
                overflow-x: auto;

                .btn-close-mobile {
                    position: absolute;
                    top: 0;
                    right: -2rem;      
                    border: transparent;
                    color: var(--primary-color);
                    background-color: transparent;
                    font-size: 30px;  
                }

                .check-now {
                    position: absolute;
                    bottom: 10px;
                    left: 50%;
                    min-width: 80%;
                    transform: translateX(-50%);
                }
            }
        }

        .cart-item {
            div {
                .quantity-controls {
                    max-width: 100px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    height: 20px;
                }

                button {
                    padding: 0;
                }
            }
        }
    }

    .container-mobile {
        position: fixed;
        top: 0;

        .container-cart {
            display: block;
            position: relative;
            top: 0;
            left: 0;
        }
    }



}
